h1 {
  text-align: center;
  max-height: 5vh;
}

.viewer__container {
  display: flex;
  justify-content: center;
  margin: 0 10%;
  padding: 30px;
}
.pagination__paginator-container {
  max-height: 15vh;
  overflow-y: scroll;
  display: flex;
  flex-direction: row;
  justify-content: center;
  flex-wrap: wrap;
}

@media (min-width: 600px) {
  .pagination__paginator-container {
    max-height: 30vh;
  }
}

.pagination__page {
  width: 30px;
  height: 30px;
  offset: none;
  margin: 4px;
}

.pagination__page:hover {
  box-shadow: 2px 2px 2px black;
  transform: scale(1.2);
}

.pagination__page:active {
  background: yellow;
  outline: none;
  border: 1px solid darkolivegreen;
}

.pagination__page--active {
  background: lightgreen;
  font-weight: 800;
}

.pagination__inputs {
  display: flex;
  align-items: center;
  padding-top: 1rem;
  flex-direction: column;
}

@media (min-width: 600px) {
  .pagination__inputs {
    flex-direction: row;
    justify-content: center;
  }
}

.pagination__inputs input {
  margin: 5px;
}
